        <main>
            <div class="block">
                <button type="button" class="desc-button" id="history5" @click="openBlock">История 5-ой серии</button>
                <div class="hidden-div history none" id="history5">
                    <p class="text">
                        BMW 1500, представленный в 1961 году, можно считать прародителем 5-й серии. Эта модель получила
                        большой
                        успех и
                        вернуло бренд с бело-синим пропеллером (BMW использовался для производства авиационных двигателей)
                        обратно в зону прибыли. Позже были добавлены модели 1800 и 2000.
                    </p>
                    <h3 class="img-headline">BMW 1500</h3>
                    <img class="bmw-img bmw-1500" src="@/img/bmw-1500.jpg" alt="BMW 1500" />
                    <p class="text">
                        В 1972 году были представлены новые названия моделей, начиная с 5-й серии. Пятая серия, известная
                        внутри
                        компании как E12, первоначально предлагалась как 520 и 520i мощностью 115 и 130 л.с. За ним
                        последовала
                        модель 525 в 1973 году с мощностью 145 л.с., а в 1974 году — 518 с мощностью 90 л.с. Год спустя был
                        доступен 528-й двигатель мощностью 165 л.с. Первая визуальная ретушь повторилась год спустя: были
                        пересмотрены передняя и задняя части. В 1977 году шестицилиндровый двигатель M20 новой конструкции
                        заменил четырехцилиндровый двигатель модели 520. Топ-моделью стала 528i с электронным впрыском
                        топлива.
                        Компания M-GmbH довольно рано начала оснащать E12 двигателями объемом от 3 до 3,5 литров. Хотя
                        изначально они не фигурировали ни в каких официальных прайс-листах, их практически можно было купить
                        из-под прилавка по запросу. Лишь позже M535i официально поступил в продажу. Всего было построено
                        около
                        700 000 E12.
                    </p>
                    <h3 class="img-headline">BMW E12</h3>
                    <img class="bmw-img bmw-e12" src="@/img/bmw-e12.jpg" alt="BMW E12" />
                    <p class="text">Следующее поколение 5-й серии E28 было представлено в мае 1981 года. По сравнению с E12
                        он,
                        похоже, изменился незначительно, поскольку использовался тот же базовый корпус. Однако были изменены
                        передняя, задняя, боковые стороны и интерьер. Все двигатели, кроме 518, теперь имеют впрыск топлива.
                        У
                        520i теперь 125 л.с., у 525i — 150 л.с., а у 528i — 184 л.с. Хотя это так не выглядит, под листовым
                        металлом произошло многое: например, передняя ось была заимствована у 7-й серии и введен индикатор
                        интервала технического обслуживания. Через несколько лет линейка двигателей была расширена за счет
                        модели 525eta, за которой позже последовали M535i и M5. После того, как было выпущено около 705 000
                        автомобилей, производство E28 было прекращено в декабре 1987 года. Из-за базового кузова Е12 внешний
                        вид
                        воспринимается старым.</p>
                    <h3 class="img-headline">BMW E28</h3>
                    <img class="bmw-img bmw-e28" src="@/img/bmw-e28.jpg" alt="BMW E28" />
                    <p class="text">
                        E34 теперь добился значительного визуального и технического прогресса. С февраля 1988 года он был
                        доступен только с шестицилиндровыми двигателями 520i (129 л.с.), 525i (170 л.с.), 530i (188 л.с.) и
                        535i
                        (211 л.с.), месяцем позже последовал дизельный 524td (115 л.с.). M5 мощностью 315 л.с. появился на
                        рынке
                        в сентябре того же года. В апреле 1990 года были представлены четырехклапанные двигатели M50,
                        обеспечивающие большую мощность у 520i (150 л.с.) и у 525i (192 л.с.). Самым маленьким двигателем
                        был
                        518i с четырьмя цилиндрами и мощностью 113 л.с., который позже был заменен более новым двигателем
                        M43 с
                        цепным приводом мощностью 115 л.с. Версия универсал Touring последовала с сентября, полноприводную
                        версию 525ix и новые дизельные 525td и 525tds можно было приобрести месяцем позже. С мая 1992 года
                        новый
                        двигатель М5 мощностью 340 л.с. обеспечивал еще большую тягу, с сентября двигатели М50 получили
                        больший
                        крутящий момент в нижнем диапазоне оборотов благодаря технологии Vanos и был проведен первый
                        небольшой
                        фейслифтинг, включавший новые наружные зеркала, среди прочего. Месяц спустя был представлен
                        двигатель
                        M60 V8, который выдавал 218 л.с. у 530i и 286 л.с. у 540i и выглядел значительно внушительнее
                        благодаря
                        более широкой решетке радиатора. Производство старого двигателя для 530i и 535i было прекращено. С
                        1995
                        года продавался Touring 518g, особенность которого заключалась в двухвалентном приводе (газ/бензин).
                        Последний фейслифтинг должен был состояться в конце 1994 года, и отныне все модели имели широкую
                        решетку
                        радиатора. В ноябре 1995 года производство E34 прекратилось, и на смену ему пришла E39. E34 Touring
                        продолжал выпускаться до 1996 года. Всего в Германии было построено ровно 1 166 312 седанов E34 и
                        124
                        656 E34 Touring. Кроме того, из комплектов деталей на заводах BMW за рубежом было изготовлено 42 470
                        седанов E34. Всего с конвейера сошло 1 333 438 E34, в том числе 11 984 M5.
                    </p>
                    <h3 class="img-headline">BMW E34</h3>
                    <img class="bmw-img bmw-e34" src="@/img/bmw-e34.jpg" alt="BMW E34" />
                </div>
            </div>
