                                S = Механика<br>
                                A = Автомат<br>
                                T = Туринг<br>
                                TA = Туринг автомат<br>
                                X = устанавливался только этот дифференциал, независимо от года выпуска и коробки
                                передач<br><br>
                                Если не указано иное, одинаковые передаточные числа применяются к автоматической коробке
                                передач седана и туристической автоматической коробке передач. Исключение: ТА (525i M50 и
                                530i M60), здесь в седане-автомате и туринг-автомате одинаковые редукторы заднего моста не
                                устанавливались.<br><br>
                                В некоторых случаях коробки передач менялись в зависимости от года выпуска, а это означало,
                                что передаточные числа заднего моста пришлось отрегулировать. Это отражено в таблице цвета
                                выделено красным и синим цветом. Красный означает перевод до изменения, синий
                                обозначает перевод после изменения.<br>
                                Изменения коснулись следующих моделей следующих годов выпуска:<br><br>
                                518i M40: 9/91<br>
                                520i M50: 1/91<br>
                                520iA M50: 9/90<br>
                                525i M50 und 525iA M50: 9/90<br>
                                525iA Туринг M50: 9/92<br>
                                535i и 535iA: 9/89<br>
                                M5 3.8 S38: 6/94<br><br>
                                Пример из таблицы:<br>
                                У 525i M50 в механической версии был дифференциал до 9/90.
                                Установлено передаточное число 3,73:1, в автоматическом варианте 4,10:1. С 9/90 это было в
                                Выключатель и автомат 3,23:1 используется также в Touring с механической коробкой передач.
                                Автомат Touring показал результат 3:46:1. Однако для 525i Touring
                                Автомат с 9/92, затем был установлен 3,38:1.<br><br>
